.nyelider {
  position: relative; }
  .nyelider .nyelider-inner {
    overflow: hidden; }
  .nyelider .nyelider-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    position: relative;
    right: 0%;
    transition: right .4s;
    z-index: 0; }
    .nyelider .nyelider-grid.with-shadow::before {
      content: "";
      width: 33%;
      height: 100%;
      display: block;
      position: absolute;
      z-index: 1;
      right: 0px;
      background-image: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2)); }
    .nyelider .nyelider-grid.with-shadow::after {
      content: "";
      width: 33%;
      height: 100%;
      display: block;
      position: absolute;
      z-index: 1;
      left: 0px;
      background-image: linear-gradient(90deg, rgba(255, 255, 255, 0.2), transparent); }
  .nyelider-item {
    flex-shrink: 0;
    flex-grow: 0;
    flex-basis: 33%; }
  .nyelider-control {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: absolute;
    left: -10px;
    right: -10px;
    top: 40%; }
    .nyelider-control .nyelider-next, .nyelider-control .nyelider-prev {
      background-color: white;
      padding: 10px;
      border: none;
      outline: none;
      cursor: pointer;
      transition: .2s;
      box-shadow: 0px 0px 3px 2px rgba(0, 0, 0, 0.2);
      border-radius: 100px; }
      .nyelider-control .nyelider-next:hover, .nyelider-control .nyelider-prev:hover {
        transform: scale(1.2); }
  .nyelider-indicator {
    padding: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center; }
    .nyelider-indicator .icon {
      width: 20px;
      height: 20px;
      margin-right: 5px;
      background-color: #333;
      border-radius: 100px;
      transition: transform .3s;
      cursor: pointer; }
      .nyelider-indicator .icon:hover {
        transform: scale(1.3);
        background-color: #1a1a1a; }
